.auth-banner{position:relative;width:100%}.auth-banner::after{content:'';position:absolute;bottom:0;right:0;width:100%;height:0.1rem;background-color:#464646}.auth-banner-content{min-height:50rem;display:-webkit-flex;display:flex;-webkit-flex-flow:column;flex-flow:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:flex-start;align-items:flex-start;padding-top:12rem;z-index:100}@media screen and (max-width: 1366px){.auth-banner-content{padding-top:10rem}}@media screen and (max-width: 1023px){.auth-banner-content{margin-left:0;padding-top:9rem}}@media screen and (max-width: 768px){.auth-banner-content{padding-top:7rem;min-height:40rem}}@media screen and (max-width: 650px){.auth-banner-content{padding-left:5rem;padding-right:5rem}}.auth-banner-content .content-heading-wrapper{height:auto;max-width:85%;margin:0 auto;overflow:hidden;margin-bottom:var(--margin-medium)}@media screen and (max-width: 1023px){.auth-banner-content .content-heading-wrapper{width:100%;max-width:100%;display:-webkit-flex;display:flex;-webkit-flex-flow:row;flex-flow:row;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}}.auth-banner-content .content-heading-wrapper .content-heading-title{text-align:center}.auth-banner-content .content-subtitle-wrapper{max-width:85%;margin:0 auto;display:-webkit-flex;display:flex;-webkit-flex-flow:row;flex-flow:row;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}@media screen and (max-width: 1023px){.auth-banner-content .content-subtitle-wrapper{display:-webkit-flex;display:flex;-webkit-flex-flow:column;flex-flow:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}}@media screen and (max-width: 650px){.auth-banner-content .content-subtitle-wrapper{width:100%;max-width:100%}}.auth-banner-content .content-subtitle-wrapper .content-subtitle{margin-right:1.5rem;text-align:center}.auth-banner-content .content-subtitle-wrapper .content-link-wrapper{position:relative}.auth-banner-content .content-subtitle-wrapper .content-link-wrapper::after{content:'';position:absolute;bottom:0;right:0;width:100%;height:0.1rem;background-color:#d3a14c;-webkit-transform:scaleX(0);transform:scaleX(0);-webkit-transform-origin:left;transform-origin:left;transition:var(--hover-transition)}.auth-banner-content .content-subtitle-wrapper .content-link-wrapper:hover::after{-webkit-transform:scaleX(1);transform:scaleX(1)}

.dashboard-main-nav{width:100%;display:grid;grid-template-columns:repeat(6, 1fr);border:0.1rem solid #464646}@media screen and (max-width: 1500px){.dashboard-main-nav{grid-template-columns:repeat(3, 1fr);border-bottom:none}}@media screen and (max-width: 767px){.dashboard-main-nav{grid-template-columns:1fr;border-bottom:none}}.dashboard-main-nav .dashboard-nav-list-item{height:100%;width:100%}.dashboard-main-nav .dashboard-nav-list-item:not(:last-child){border-right:0.1rem solid #464646}@media screen and (max-width: 1500px){.dashboard-main-nav .dashboard-nav-list-item{border-bottom:0.1rem solid #464646}}.dashboard-main-nav .dashboard-nav-list-item .dashboard-nav-link{width:100%;height:100%;padding:3.5rem 0;display:-webkit-flex;display:flex;-webkit-flex-flow:row;flex-flow:row;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;transition:var(--hover-transition)}.dashboard-main-nav .dashboard-nav-list-item .dashboard-nav-link:hover{background-color:#464646}.dashboard-main-nav .dashboard-nav-list-item .dashboard-nav-link .label-medium{font-weight:400}.dashboard-main-nav .dashboard-nav-list-item .dashboard-nav-link.selected{background-color:#fafafa}.dashboard-main-nav .dashboard-nav-list-item .dashboard-nav-link.selected .label-medium{color:#1a1a1a}

.general-error{width:100%;height:auto;margin:var(--margin-medium) 0;padding:var(--general-padding-small);background-color:#7d7d7d}.general-error-inner{width:100%;display:-webkit-flex;display:flex;-webkit-flex-flow:row;flex-flow:row;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:center;align-items:center}.general-error-inner .link-label{text-transform:uppercase}.general-error-inner .link-label .label{margin-left:1rem;text-transform:none}

.button-primary{position:relative;display:-webkit-flex;display:flex;-webkit-flex-flow:row;flex-flow:row;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;padding:1rem 0}.button-primary::before,.button-primary::after{content:'';position:absolute;bottom:0;left:0;right:0;width:100%;height:0.1rem;background-color:#464646}.button-primary::after{height:0.2rem;background-color:#fafafa;-webkit-transform:scaleX(0);transform:scaleX(0);-webkit-transform-origin:center;transform-origin:center;transition:var(--hover-transition)}.button-primary-label{margin-right:var(--margin-small);transition:var(--hover-transition)}.button-primary:hover::after{-webkit-transform:scaleX(1);transform:scaleX(1)}.button-primary:hover .button-primary-label{margin-right:var(--margin-medium)}

